@import '../../global.scss';

.technologyContainer {
    position: relative;
    width: 100%;
    overflow-x: hidden;

    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    .intro,
    .background,
    .exploreMore {
        background-color: white;
        height: 100vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        margin: 0;
        padding: 0;
        box-sizing: border-box;

        @media (min-width: $mobile) {
            padding: 0 $page-padding-x-m;
        }

        @media (min-width: $tablet-small) {
            padding: 0 $page-padding-x-ts;
        }

        @media (min-width: $tablet-large) {
            padding: 0 $page-padding-x-tl;
        }

        @media (min-width: $desktop-small) {
            padding: 0 $page-padding-x-ds;
        }

        @media (min-width: $desktop-large) {
            padding: 0 $page-padding-x-dl;
        }

        h1 {
            font-style: normal;
            font-weight: 500;
            font-size: 88px;
            line-height: 140%;
            letter-spacing: -0.015em;
            width: 75%;
            text-align: center;
            margin: 0 auto;
            padding: 0 2rem;
            max-width: 1200px;

            @media (min-width: $mobile) {
                font-size: 38px;
                width: 100%;
            }

            @media (min-width: $tablet-small) {
                font-size: 50px;
                width: 75%;
            }

            @media (min-width: $tablet-large) {
                font-size: 68px;
                width: 75%;
            }

            @media (min-width: $desktop-small) {
                font-size: 88px;
                width: 75%;
            }

            @media (min-width: $desktop-large) {
                font-size: 88px;
                width: 75%;
            }
        }
    }

    .intro {
        z-index: 1;
    }

    .background {
        z-index: 2;
        color: white;

        video {
            height: 100%;
            width: 100%;
            position: absolute;
            object-fit: cover;
            z-index: -1;
            filter: brightness(0.5);
            top: 0;
            left: 0;
        }
    }

    .exploreMore {
        justify-content: center;
        flex-direction: column;
        z-index: 3;
        color: black;

        h1 {
            text-align: center;
        }

        img {
            margin-top: 2rem;
            width: 40px;
            height: 40px;
        }
    }

    .background,
    .exploreMore {
        mask-image: url('../../assets/Technology/circle.svg');
        mask-repeat: no-repeat;
        mask-position: center;
        mask-size: 0%;
        -webkit-mask-image: url('../../assets/Technology/circle.svg');
        -webkit-mask-repeat: no-repeat;
        -webkit-mask-position: center;
        -webkit-mask-size: 0%;
    }

    .fullScreen {
        height: 100vh;
        width: 100%;
    }

    .statsContainer {
        position: relative;
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        padding: 2rem;
        overflow: hidden;
        box-sizing: border-box;

        @media (min-width: $mobile) {
            flex-direction: column;
        }

        @media (min-width: $tablet-small) {
            flex-direction: row;
        }

        @media (min-width: $tablet-large) {
            flex-direction: row;
        }

        @media (min-width: $desktop-small) {
            flex-direction: row;
        }

        @media (min-width: $desktop-large) {
            flex-direction: row;
        }

        padding: 0 $page-padding-x-dl;

        @media (min-width: $mobile) {
            padding: 500px $page-padding-x-m;
        }

        @media (min-width: $tablet-small) {
            padding: 0 $page-padding-x-ts;
        }

        @media (min-width: $tablet-large) {
            padding: 0 $page-padding-x-tl;
        }

        @media (min-width: $desktop-small) {
            padding: 0 $page-padding-x-ds;
        }

        @media (min-width: $desktop-large) {
            padding: 0 $page-padding-x-dl;
        }

        .sensorContainer {
            width: 100%;
            max-width: 800px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 auto;

            img {
                width: 100%;
                height: auto;
                object-fit: contain;
            }
        }

        .lineContainer {
            display: flex;
            flex-direction: column;

            .line:not(:last-child) {
                margin-bottom: 68px;
            }

            .line {
                height: 400px;
                width: 1px;
                background: #9F9F9F;
            }
        }

        .dataContainer {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 4rem;
            flex-wrap: wrap;
            width: 100%;
            max-width: 1200px;
            padding: 2rem;
            margin: 0 auto;

            .dataPoint {
                text-align: center;
                min-width: 200px;

                h3 {
                    font-size: 3rem;
                    margin-bottom: 0.5rem;
                }

                p {
                    font-size: 1.2rem;
                    color: #666;
                }
            }
        }
    }

    .imageCards {
        position: relative;
        width: 100%;
        padding: 4rem 2rem;
        box-sizing: border-box;

        .imageRow {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 4rem;
            margin-bottom: 6rem;
            opacity: 0;
            transform: translateY(50px);
            transition: all 0.8s ease-out;
            max-width: 1200px;
            margin-left: auto;
            margin-right: auto;

            &.slideIn {
                opacity: 1;
                transform: translateY(0);
            }

            .imageDesc {
                flex: 1;
                max-width: 500px;

                h4 {
                    font-size: 2rem;
                    margin-bottom: 1rem;
                }

                p {
                    font-size: 1.2rem;
                    line-height: 1.6;
                    color: #666;
                }
            }

            .image {
                flex: 1;
                max-width: 600px;

                img {
                    width: 100%;
                    height: auto;
                    object-fit: cover;
                    border-radius: 8px;
                }
            }

            &:nth-child(even) {
                flex-direction: row-reverse;
            }
        }

        .banner {
            width: 100%;
            height: 400px;
            margin: 4rem 0;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            position: relative;
            overflow: hidden;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: rgba(0, 0, 0, 0.2);
                z-index: 1;
            }

            &.banner1 {
                background-image: url('../../assets/Technology/banner1.png');
            }

            &.banner2 {
                background-image: url('../../assets/Technology/banner2.png');
            }

            @media (min-width: $tablet-small) {
                height: 500px;
            }

            @media (min-width: $desktop-small) {
                height: 600px;
            }
        }
    }

    .achievement {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 4rem 2rem;
        text-align: center;
        box-sizing: border-box;

        header {
            max-width: 800px;
            margin: 0 auto;
            width: 100%;

            h2 {
                font-size: 3.5rem;
                margin-bottom: 2rem;

                span {
                    color: #007bff;
                }
            }

            h3 {
                font-size: 2rem;
                margin-bottom: 2rem;
                color: #666;
            }

            p {
                font-size: 1.4rem;
                line-height: 1.8;
                color: #444;

                b {
                    color: #000;
                }
            }
        }

        .last {
            margin-top: 4rem;
            font-size: 1.8rem;
            color: #666;
        }
    }
}

@media (max-width: 768px) {
    .technologyContainer {

        .intro,
        .background,
        .exploreMore {
            h1 {
                width: 100%;
                padding: 0 1rem;
            }
        }

        .statsContainer {
            .dataContainer {
                gap: 2rem;
                padding: 1rem;

                .dataPoint {
                    min-width: 150px;

                    h3 {
                        font-size: 2.5rem;
                    }

                    p {
                        font-size: 1rem;
                    }
                }
            }
        }

        .imageCards {
            padding: 2rem 1rem;

            .imageRow {
                flex-direction: column;
                gap: 2rem;
                margin-bottom: 4rem;
                padding: 0 1rem;

                &:nth-child(even) {
                    flex-direction: column;
                }

                .imageDesc {
                    max-width: 100%;
                    text-align: center;

                    h4 {
                        font-size: 1.8rem;
                    }

                    p {
                        font-size: 1.1rem;
                    }
                }

                .image {
                    max-width: 100%;
                }
            }

            .banner {
                height: 250px;
                margin: 2rem 0;
            }
        }

        .achievement {
            padding: 2rem 1rem;

            header {
                h2 {
                    font-size: 2.5rem;
                }

                h3 {
                    font-size: 1.5rem;
                }

                p {
                    font-size: 1.2rem;
                }
            }

            .last {
                font-size: 1.4rem;
            }
        }
    }
}